  height:100vh;
  overflow: hidden;
  position: relative;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  display: -ms-flexbox;
  display: flex;
}
.bulb:nth-last-child(n+2),
.bulb:nth-last-child(n+2) ~ .bulb {
  width:50vw;
}

.bulb > * {
  -ms-flex:none;
      flex:none;
  margin:10px 0;
}

.background-color {
